<template>
	<view>
		<my-tab 
		:activeTabIndex="activeTabIndex"
		:tabs="tabs" :backgroundColor="'white'" 
		:isTabFixed="isTabFixed" :offsetTop="offsetTop"
			@activeTabIndexChange="tabIndexChange"></my-tab>
		<scroll-view 
			:refresher-triggered="triggered" 
			@refresherpulling="onPulling"
			@refresherrefresh="onRefresh" 
			@refresherrestore="onRestore" 
			scroll-y="true" 
			refresher-enabled="true">
			<!-- 推荐 -->
			<swiper 
			class="swiper-box"
			:current="activeTabIndex" 
			@change="swiperIndexChange">
			<!-- 推荐列表 -->
				<swiper-item>
					<view class="tabs_container">
						<view v-for="item in subscribeList" :key="item.id" class="subscribeItem">
							<!-- 推荐item标题 -->
							<text class="subscribeItem_title">{{item.title}}</text>
							<!-- 推荐item额外信息 -->
							<view class="subscribeItem_info">
								<uni-tag :text="item.tag" :type="item.tagType" v-if="item.tag" class="subscribeItem_tag">
								</uni-tag>
								<text>{{item.source}}</text>
								<text v-if="item.readNum">{{item.readNum}}阅读</text>
								<text>{{item.time}}</text>
							</view>
						</view>
					</view>
				</swiper-item>
				<!-- 要闻列表 -->
				<swiper-item>
					<view class="tabs_container">
						<view v-for="item in mainNewsList" :key="item.id" class="subscribeItem">
							<!-- 推荐item标题 -->
							<text class="subscribeItem_title">{{item.title}}</text>
							<!-- 推荐item额外信息 -->
							<view class="subscribeItem_info">
								<uni-tag :text="item.tag" :type="item.tagType" v-if="item.tag" class="subscribeItem_tag">
								</uni-tag>
								<text>{{item.source}}</text>
								<text v-if="item.readNum">{{item.readNum}}阅读</text>
								<text>{{item.time}}</text>
							</view>
						</view>
					</view>
				</swiper-item>
			</swiper>
			
		</scroll-view>
	</view>
</template>

<script>
	import MyTab from '../my-tab/my-tab.vue'
	export default {
		name: "MyTabWithContent",
		created() {
			this.getSubscribeList()
		},
		props: {
			// tabs
			tabs: {
				type: Array
			},
			isTabFixed: {
				type: false
			},
			offsetTop: {
				type: String
			},
			whereFixed: {
				type: Number
			}
		},
		components: {
			"my-tab": MyTab
		},
		data() {
			return {
				// 激活的tab序号
				activeTabIndex: 0,
				// 是否在刷新状态中
				isRefreshing: false,
				// 刷新开关
				triggered: false,
				// 推荐列表
				subscribeList: [],
				// 要闻列表
				mainNewsList: [],
				// 自选列表
				customSelectList: [],
				// 直播列表
				liveList: [],
				// 课堂列表
				lessonList: [],
				// 服务列表
				serviceList: []

			};
		},
		methods: {
			tabIndexChange(index) {
				console.log('改变了index:', index)
				this.activeTabIndex = index
				uni.pageScrollTo({
					// #ifdef MP
					scrollTop: this.whereFixed+1,
					// #endif
					// #ifndef MP
					scrollTop: this.whereFixed,
					// #endif
					duration: 300
				})
				this.callRefresh()
			},
			swiperIndexChange(e){
				console.log(e.detail.current)
				this.tabIndexChange(e.detail.current)
			},
			callRefresh() {
				// #ifdef MP
				this.onPulling()
				this.onRefresh()
				// #endif
				
				// #ifndef MP
				// this.$nextTick(() => {
				// 	console.log(this.$refs.scrollviewRef)
				// 	this.$refs.scrollviewRef.$listeners.refresherpulling()
				// 	this.$refs.scrollviewRef.$listeners.refresherrefresh()
				// })
				this.onPulling()
				this.onRefresh()
				// #endif
				

			},
			// 下拉钩子
			onPulling() {
				console.log('pull')
				this.triggered = true
			},
			// 刷新中的钩子
			onRefresh() {
				if (this.isRefreshing) {
					return
				}
				console.log('refreshing')
				this.isRefreshing = true
				// console.log("onRefresh",e);
				if (this.activeTabIndex === 0) {
					setTimeout(() => {
						this.getSubscribeList()
						this.isRefreshing = false
						this.triggered = false
					}, 3000)
				} else if (this.activeTabIndex === 1) {
					setTimeout(() => {
						this.getMainNewsList()
						this.isRefreshing = false
						this.triggered = false
					}, 3000)
				}else{
					this.isRefreshing = false
					this.triggered = false
				}
			},
			onRestore() {
				console.log('restore')
			},
			getSubscribeList() {
				this.subscribeList = [{
						id: '0',
