						source: '证券时报',
						tag: '重磅',
						tagType: 'primary',
						time: '08:07'
					}
				]
			},
			getMainNewsList() {
				this.mainNewsList = [{
						id: '0',
						title: '招商银行谭卓：明年美国“滞涨”将不再是问题',
