</script>

<style lang="scss">
	.swiper-box{
		height: 2000rpx;
		.tabs_container {
			padding: 0 20rpx;
		
			.subscribeItem {
				border-bottom: 1px solid #ccc;
				box-sizing: border-box;
				padding: 30rpx 0;
		
				.subscribeItem_title {
